import { Component } from '@angular/core';
import { NavController, ModalController  } from 'ionic-angular';
import { SQLite, SQLiteObject } from '@ionic-native/sqlite';

import { HomePage } from '../home/home';
import { Notes } from '../../class/notes';


@Component({
  selector: 'page-modal',
  templateUrl: 'modal.html'
})
export class ModalPage {
  public banco: string;

  constructor(public navCtrl: NavController,
  private sqlite: SQLite) { 
    
  } 
  
  note = {
    title : "",
    content : ""
  }


  addNotes(n){
      var obj = new Notes(n.title, n.content);
      console.log(obj);
      this.sqlite.create({name: 'Notes.db', location: 'default'}).then(
        (db: SQLiteObject ) => { 
          db.executeSql('INSERT INTO tbl_Notes (title, content) VALUES (?,?)', [obj]);
          this.banco = "Inseriu !!"
          console.log("Success insert table notes !!") 
        }).catch((x) => { 
          console.log("Error insert table notes !!") 
       });
  }

  backHome(){
    this.navCtrl.pop();
  }

}





