import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { SQLite, SQLiteObject } from '@ionic-native/sqlite';

import { TabsPage } from '../pages/tabs/tabs';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = TabsPage;
  
  
  constructor(platform: Platform, 
    statusBar: StatusBar, 
    splashScreen: SplashScreen, 
    private sqlite: SQLite) {

    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
      this.openDb();
  })
 }

 openDb() {
    this.sqlite.create({name: 'Notes.db', location: 'default'}).then(
    ( db: SQLiteObject ) => { 
      db.executeSql('CREATE TABLE IF NOT EXISTS tbl_Notes (id integer primary key, title text, content text)', {}).then(() =>  
      db.executeSql('INSERT INTO tbl_Notes VALUES(?,?,?)', [null, 'Titulo', 'Conteudo'] ))
      console.log("Success create table notes !!") 
    }).catch((x) => { 
      console.log("Error create table notes !!") 
   });
  }

}



