import { Component } from '@angular/core';
import { NavController, ModalController  } from 'ionic-angular';
import { ModalPage } from '../modal/modal';
import { SQLite, SQLiteObject } from '@ionic-native/sqlite';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  public arr = [];
  public banco: string;
  public b: string; 

  constructor(public navCtrl: NavController, 
    public modalCtrl: ModalController,
    public sqlite: SQLite) {

      this.sqlite.create({name: 'Notes.db', location: 'default'}).then(
        ( db: SQLiteObject ) => {
          db.executeSql('INSERT INTO tbl_Notes VALUES(?,?,?)', [null, 'TituloUm', 'ConteudoUm'] );          
          this.banco = "Carregou !!";
          db.executeSql('SELECT * FROM tbl_Notes', {}).then(x => { 
            this.b = "Nao deu !!! s";  
            if(x.rows.length > 0){
               this.b = x;
               for(var i=0; i < x.rows.length; i++){
                 this.arr.push({ id: x.rows.item(i).id, title: x.rows.item(i).title, content: x.rows.item(i).content});
               } 
              };
          }) 
            
          console.log("Success create table notes !!") 
        }).catch((x) => {
          this.banco = "Ñ Carregou !!"; 
          console.log("Error create table notes !!") 
       });

  }



  addNotes() {
    let modal = this.modalCtrl.create(ModalPage);
    modal.present();
  }
}



